
.container{
    height: 70vh;
    width: 100%;
    perspective: 1000px;
}

.card{
    height: 100%;
    width: 100%;
    position: relative;
    transition: transform 1000ms;
    transform-style: preserve-3d;
    box-shadow: 0 0 10px 3px rgba(50,50,50,0.5);
}

.container:hover > .card{
    transform: rotateY(180deg);
}


.front, .back{
    height: 100%;
    width: 100%;
    position: absolute;
    backface-visibility: hidden;
}

.back{
    background-color: black;
    transform: rotateY(180deg);
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.imgStyle{
    height: 100%;
    width: 100%;
}

.heading{
    color: white;
    font-size: 24px;
    font-weight: bold;
    padding: 1rem 2rem;
}

.infoText{
    color: white;
    font-size: 20px;
    padding: 1rem 2rem;
}